.navbar {
  background-color: #333;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap; 
}

.logo-container {
  flex-grow: 1;
  display: flex;
  justify-content: left;
  margin-left: 2rem;
}

.logo-text {
  background-color: #333;
  color: #fff;
  font-size: 2rem; 
  font-weight: bold;
  margin: 0;
}

.nav-links {
  list-style: none;
  display: flex;
  justify-content: flex-end;
  margin-right: 1rem;
  flex-wrap: wrap; 
}

.nav-links li {
  margin-left: 15px;
}

.nav-links button {
  background-color:#333; 
  border: none; 
  color: white; 
  padding: 10px 20px;
  font-size: 1.2rem; 
  cursor: pointer;
}

.nav-links button:hover {
  text-decoration: underline; 
  color: #ddd; 
}

.nav-links button:focus {
  outline: none; 
}

@media (max-width: 768px) {
  .navbar {
    padding: 0.5rem; 
  }

  .logo-text {
    font-size: 1.5rem;
  }

  .nav-links {
    margin-right: 0;
    justify-content: center; 
    width: 100%; 
    padding-top: 0.5rem; 
    flex-direction: row; 
  }

  .nav-links li {
    margin-left: 10px; 
  }

  .nav-links button {
    font-size: 1.5rem; 
    padding: 10px;
  }
}

@media (max-width: 480px) {
  .navbar {
    flex-direction: column; 
    align-items: center; 
    padding: 1rem; 
  }

  .logo-container { 
    margin-bottom: 1rem;

  }

  .logo-text {
    font-size: 1.5rem; 

  }

  .nav-links {
    padding-top: 0; 
    flex-direction: column; 
    width: 100%; 
    align-items: center; 
  }

  .nav-links li {
    margin-left: 0; 
    margin-bottom: 0.5rem; 
    text-align: center; 
  }

  .nav-links button {
    font-size: 1.2rem; 
    padding: 10px; 
    width: 100%; 
  }
}
