* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

.contact-container {
    padding: 20px;
    text-align: center;
    height: 100vh;
    background-color: #333;
  }
  
  .contact-container h1 {
    margin-top: 10rem;
    color: wheat;
    font-size: 36px;
    font-size: 10rem;
    align-items: center;
    justify-content: center;
    
  }

  @media (max-width: 1200px) {
    .contact-container h1 {
      font-size: 8rem;
    }
  }

  @media (max-width: 768px) {
    .contact-container h1 {
      font-size: 5rem;
    }
  }
  
  @media (max-width: 480px) {
    .contact-container h1 {
      font-size: 3rem;
    }
  }